<template lang="html">
  <div class="card daycard today">
    <div class="cardimg">
      <strong>{{ today }}</strong>
    </div>
    <div class="container user">
      <ul class="userlist">
        <li class="listentry">some user</li>
        <li class="listentry">some other user</li>
      </ul>
    </div>
  </div>

</template>

<script>
export default {
  props: {
    day: Number
  },
  data: function() {
    return {
      today: 0
    }
  },
  mounted() {
    this.today = (new Date()).getDate();
  }
}
</script>

<style lang="css">
.daycard {
  width: 150px;
  height: auto;
  display: inline-block;
  color: #dd0404;
  font-size: 25px;
}
.user {
  font-size: 11px;
}
.today {
  border-radius: 5px;
  border-color: #dd0404;
}
.userlist {
  list-style-type: none;
  padding-left: 0px;
}
.listentry {
  border-bottom: 1px solid;
  color: #444;
}
@media screen and (max-width: 600px) {
  .daycard {
    width: 45px;
    font-size: 15px;
  }
}
</style>
